  text-decoration: none;
  cursor: pointer;
  margin-bottom: 10px;
  border-radius: 10px;
  transition: 0.2s;
}

.sidebar .menu-item:hover {
  background-color: #e9ecef;
  color: #1f2c38;
}

.sidebar .collapse .dropdown-item {
  padding: 6px 30px;
  margin-bottom: 6px;
  color: #ffffff;
  text-decoration: none;
  /* AÃ±adir un punto antes del texto */
}
.sidebar .collapse .dropdown-item::before {
  content: "â€¢"; /* Esto aÃ±ade el punto */
  margin-right: 10px; /* Espacio entre el punto y el texto */
  color: #ffffff; /* Color del punto */
}
.sidebar .collapse .dropdown-item:last-of-type {
  margin-bottom: 0;
}
.sidebar .collapse .dropdown-item:hover {
  background-color: transparent;
  color: #ff9100;
}

.dropdown-item {
  padding-left: 30px;
  color: #ffffff;
  padding: 6px;
  transition: 0.2s;
  font-size: 14px;
}
.dropdown-item::before {
  content: "â€¢"; /* Esto aÃ±ade el punto */
